.container {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #EBF0F4;
    padding-top: 20vh;
}

.pokeball {
    height: 250px;
    width: 250px;
    border: 13px solid #383b4f;
    border-radius: 50%;
    background-image: linear-gradient(
        #f51939 125px,
        white 125px
    );
    position: relative;
    animation: load 2s infinite;
}
@keyframes load {
    50%{
        transform: scale(0.7);
    }
}
.pokeball:before {
    position: absolute;
    content: "";
    height: 25px;
    width: 230px;
    background-color: #383b4f;
    top: 102.5px;
}

.pokeball:after {
    position: absolute;
    content: "";
    height: 90px;
    width: 90px;
    background-image: radial-gradient(
        #ffffff 0px, #ffffff 10px,
        #383b4f 10px, #383b4f 15px,
        #ffffff 15px, #ffffff 32px,
        #383b4f 32px
    );
    border-radius: 50%;
    top: 70px;
    left:70px;
}
